/**
 * Returns a value from object by provided path
 */
export const getValue = <Item extends Record<any, any> | undefined, Value = any>(
  item: Item,
  key: string,
): Value | undefined => {
  const fields = key.split(/[.[\]]/g).filter((field) => field !== '');
  return fields.length ? fields.reduce((itemData, field) => itemData?.[field], item) : undefined;
};
